                    {/*    }*/}
                    {/*</FormItem>*/}
                    {/*<FormItem  label=''>*/}
                    {/*    {getFieldDecorator('stop_work_time',{*/}
                    {/*        initialValue:moment(),*/}
                    {/*    })*/}
                    {/*    (<DatePicker showTime format='YYYY-MM-DD HH:mm'/>)*/}
                    {/*    }*/}
                    {/*</FormItem>*/}
                    {/*<FormItem  label=''>*/}
                    {/*    {getFieldDecorator('stop_at',{*/}
                    {/*        initialValue:moment(),*/}
                    {/*    })*/}
                    {/*    (<DatePicker showTime format='MM-DD HH:mm'/>)*/}
